::after,
::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
#heading {
    align-items: center;
    justify-content: center;
    color: white;
    display: flex;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    
}
body {
    font-family: 'Courier New', Courier, monospace;
    background: black
}


.slider {
    width: 400px;
    
    position: relative;
    overflow: hidden;
    margin: auto ;
    background:white;

    
}

.container .img-container img {
    height: 300px;
    width: 100%;
    margin: auto;
}

.container .text-container {
    background: rgb(218, 236, 50);
    padding: 20px;
}

.container .text-container .slider-details p, h4{
    padding: 5px;
}

.container .text-container .slider-details h4 {
    color:brown;
    font-size: 32px;
}

/* buttons */

.container .text-container .button-container {
    background-color: grey;
    display: flex;
    justify-content: space-between;
    position: absolute;
    width: 30%;
    top: 60%;
    right: 0;
    padding: 10px 10px;
}

.container .text-container .button-container button {
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    border: 1px solid black;
    outline: none;
    background-color: rgb(250, 10, 10);
    color: aliceblue;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}
.container .text-container .button-container button  :hover {
    color: black;
}

.container .text-container .slider-details .button-container button :hover {
    background-color: #fff;
    color: black;
}